import { useEffect, useState } from 'react';
import './DownArrow.css';

function DownArrow() {
  const [scrollY, setScrollY] = useState(0);
  
  const opacityControl = (x) => {
    setScrollY(document.getElementById('main_scroll').scrollTop);
  }

  useEffect(() => {
      if (typeof window !== "undefined") {
        document.getElementById('main_scroll').addEventListener('scroll', opacityControl);
  
        // cleanup function
        return () => {
          window.removeEventListener('scroll', opacityControl);
        };
      }
  }, [opacityControl]);
  
  return <div className="down-arrow" style={{opacity: window.innerWidth > 1000 ? (100-scrollY/2).toString()+"%" : "0%"}}/>;
}
  
export default DownArrow;