import { useState } from 'react';
import DownArrow from './components/DownArrow';
import './App.css';

const {
  hands_names,
  cascades_names,
  patterns,
  variations,
  samePattern,
  findPattern,
  generateName,
  generatePatternDescription,
} = require('./tricks');

const title_image_index = Math.floor(Math.random()*9.99999999);
function App() {
  // const trick_selected = JSON.parse(decodeURIComponent(GetUrl('trick') || "null"));
  const [trick_selected, set_trick_selected] = useState(null);

  const renderIcons = (trick) => {
    if (trick.type === "pattern"){
      return (
        <div onClick={() => set_trick_selected(trick)} style={{cursor: "pointer"}}>
          <img src={require(`../assets/icons/${trick.cascades}.png`)} style={{width: "100%", margin: "0vw 0vw 0vw 0vw"}}/>
          <img src={require(`../assets/icons/${trick.hands}.png`)} style={{width: "100%", margin: "0vw 0vw 0vw 0vw"}}/>
        </div>
      )
    } else if (trick.type === "variation"){
      return (
        <div style={{display: "flex", flexDirection: "row", alignItems: "center"}}>
          {renderIcons(findPattern(trick))}
          <div style={{fontSize: "30pt", fontWeight: "bold", marginRight: "10%", marginLeft: "10%"}}>
            {" + "}
          </div><div style={{fontSize: "30pt", fontWeight: "bold"}}>
            {trick.name}
          </div>
        </div>
      )
    } else if (trick.type === "transition"){
      return (
        <div style={{display: "flex", flexDirection: "row", alignItems: "center"}}>
          {renderIcons(findPattern(trick))}
          <div style={{fontSize: "30pt", fontWeight: "bold", marginRight: "10%", marginLeft: "10%"}}>
            {" → "}
          </div>
          {renderIcons(findPattern(trick.into))}
        </div>
      )
    }
  }


  const renderTricksInPattern = (trick) => {
    const trick_list = variations.filter(t => (samePattern(trick, t) && (!t.name || t.name != trick.name)));
    if (trick_list.length > 0){
      return trick_list.map((trick) => {
        return (
          <div
            key={trick.hands + trick.cascades + (trick.type==="transition" ? trick.into.hands + trick.into.cascades : trick.name)}
            onClick={() => set_trick_selected(trick)}
            style={{cursor: "pointer", color: trick.untested ? "#666" : "#000"}}
          >
            {generateName(trick) + (trick.untested ? " (not tested)" : "")}
          </div>
        )
      })
    } else {
      return (
        <div>
          No tricks yet
        </div>
      )
    }
  }

  const renderPatternTable = () => {
    return(
      <table style={{tableLayout: "fixed", width: "100%", fontSize: "14pt", backgroundColor: "#fff3", boxSizing: "border-box", padding: "1vw"}}>
        <col width="20pt"/><col width="auto"/><col width="auto"/><col width="auto"/><col width="auto"/>
        <tbody>
          <tr>
            <td/>
            <td/>
            <th colSpan="3">hands</th>
          </tr>
          <tr>
            <td/>
            <td/>
            {Object.entries(hands_names).map(([hands, hands_text]) => {
              return  (
              <th scope="col" style={{padding: "0.75vw"}}>
                {hands_text}
                <img src={require(`../assets/icons/${hands}.png`)} style={{width: "100%", marginTop: "0.75vw"}}/>
              </th>
              );
            })}
          </tr>

          {Object.entries(Object.entries(cascades_names)).map(([index, [cascades, cascade_text]]) => {
            return  (
              <tr style={{width: "100%"}} key={cascades}>
                {index == 0 && (
                  <th rowSpan="3" style={{}}>
                    <div className='rotate'>
                      cascades
                    </div>
                  </th>
                )}
                <th scope="row" style={{padding: "0.75vw"}}>
                  <img src={require(`../assets/icons/${cascades}.png`)} style={{width: "100%"}}/>
                  {cascade_text}
                </th>
                {Object.entries(hands_names).map(([hands, hands_text]) => {
                  const highlight = trick_selected && trick_selected.hands === hands && trick_selected.cascades === cascades;
                  return  (
                    <td 
                      key={hands}
                      style={{
                        border: "1px solid black",
                        cursor: "pointer",
                        padding: "0.75vw",
                        verticalAlign: "middle",
                        backgroundColor: highlight ? "#CCCCCC" : "#FFFFFF"}}
                      onClick={() => set_trick_selected(findPattern({hands: hands, cascades: cascades}))}
                    >
                      <div style={{aspectRatio: 1.2, display: "flex", alignItems: "center"}}>
                        {renderIcons(findPattern({hands: hands, cascades: cascades}))}
                      </div>
                    </td>
                  );
                })}
              </tr>
            );
          })}
        </tbody>
      </table>
    )
  }


  const gif_render = trick_selected && trick_selected.gif && (
      <div style={{maxWidth: "400px", marginBottom: "2vh"}}>
        <img width="100%" src={require(`../assets/gifs/${trick_selected.gif}.gif`)}/>
      </div>
  )

  const variations_render = trick_selected && (
    <div style={{marginTop: "2vh", marginBottom: "2vh"}}>
      <div style={{fontSize: "15pt", fontWeight: "bold"}}>
        {trick_selected.type === "pattern" ? "Tricks in this pattern" : "Other tricks in this pattern"}
        <span style={{fontSize: "12pt", fontWeight: "normal"}}> (click for details) </span>
      </div>
      <div style={{marginTop: "1vw", marginBottom: "2vw"}}>
        {renderTricksInPattern(trick_selected)}
      </div>
    </div>
    
  )

  

  return (
    <div id="main_scroll" style={{height: "100vh", display: "flex", flexDirection: "column", alignItems: "center", overflow: "scroll", backgroundColor: "#ffecaf"}}>
      <div style={{maxWidth: "100%", width: "100%", display: "flex", flexDirection:"column", alignItems: "center"}}>
        <div style={{width: "100%", display: "table", flexDirection: "row"}}>
          <div style={{width: "50%", display: "table-cell", verticalAlign: "middle", textAlign: "center", position: "relative"}}>
            <div style={{fontFamily: '"Abril Fatface", serif', fontWeight: "400", fontSize: "3.5vw", flex: 1}}>
              Entangled Juggling
            </div>
            <DownArrow/>
          </div>
          <div style={{maxWidth: "50%", display: "table-cell", textAlign: "right"}}>
            <img 
              src={require(`../assets/title_image/title_image_${title_image_index}.jpeg`)}
              style={{width: "50vw", height: "50vw", maxHeight: "100vh"}}
            />
          </div>
        </div>


        <div style={{maxWidth: "1000px", width: "100%", display: "flex", flexDirection:"column", justifyContent: "center", padding: "3vw", boxSizing: "border-box"}}>
          <div style={{fontSize: "14pt", marginTop: "5vw"}}>
            <i>Entangled juggling</i> is a form of collaborative juggling where two jugglers are either physically linked by having their arms crossed or share their juggling patterns in such a way that each cascade involves hands from both jugglers. This technique can result in a slightly confusing synchronized display, often combining both arm entanglement and interconnected juggling sequences. 
          </div>
          <div style={{fontSize: "16pt", fontWeight: "bold", marginTop: "6vh", marginBottom: "4vh"}}>
            Pattern Overview
          </div>
          <div id="table_container" style={{maxWidth: "600px", alignSelf: "center"}}>
            {renderPatternTable()}
          </div>
          
          { trick_selected ?
            <div id="trick_container" style={{marginTop: "6vh"}}>
              {/* {trick_icons} */}
              <div style={{fontSize: "16pt", fontWeight: "bold", marginBottom: "2vh"}}>
                {generateName(trick_selected)} <p/>
              </div>
              { trick_selected.type === "pattern" &&
                <div style={{fontSize: "14pt", marginBottom: "3vh"}}>
                  {trick_selected.description || generatePatternDescription(trick_selected)}
                </div>
              }
              { trick_selected.untested && 
                <div>
                  This trick has not been tested <p/>
                </div>
              }
              { (trick_selected.type === "transition") && 
                <div style={{marginTop: "3vh", maxWidth: "500px", marginBottom: "2vh"}}>
                  {renderIcons(trick_selected)}
                </div>
              }
              <div style={{display: "flex", flexDirection: "row", alignItems: "flex-start", justifyContent: "space-around"}}>
                {gif_render}
                {trick_selected.type === "pattern" && window.innerWidth >= 700 &&
                 variations_render
                }
              </div>
              
              {trick_selected.type === "pattern" && window.innerWidth < 700 &&
                 variations_render
              }

              {trick_selected.video && (
                <div style={{width: "100%", height: "90vh"}}>
                  <iframe width="100%" height="100%" src={`https://www.youtube.com/embed/${trick_selected.video}`} title="YouTube video player" frameborder="0" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture; web-share" referrerPolicy="strict-origin-when-cross-origin" allowFullScreen></iframe>
                </div>
              )}

              {!trick_selected.video && !trick_selected.gif &&
                <div>
                  No videos or animations yet
                </div>
              }
              
              {trick_selected.type === "variation" &&
                <div style={{marginTop: "2vw"}}>
                  {variations_render}
                </div>
              }

            </div> :
            <div style={{fontSize: "14pt", marginTop: "6vh", textAlign: "center"}}>
              ↑ Pick a base pattern for more information ↑
            </div>
          }
          <div style={{height: "10vh"}}/>
        </div>
      </div>
    </div>
  
  );
}

export default App;
