export function generateName(trick, only_base = false){
  if (only_base || trick.type === "pattern"){
    return `${hands_names[trick.hands]} ${cascades_names[trick.cascades]}` + (only_base ? "" : " cascades");
  } else if (trick.type === "variation"){
    return generateName(trick, true) + " " + trick.name;
  } else if (trick.type === "transition"){
    return  generateName(trick, true)
          + " → " 
          + generateName(trick.into, true);
  }
}

export function generatePatternDescription(trick){
  var hands_text;
  var cascades_text; 

  if (trick.hands === "aabb"){
    hands_text = "side by side"
  } else if (trick.hands === "abab"){
    hands_text = "side by side with their adjacent arms crossed"
  } else {
    hands_text = "in front of each other, facing the same direction. The back juggler reaches around the front person with both hands, positioning their hands on the outside of the front juggler's hands"
  }
  if (trick.cascades === "separated_parallel"){
    cascades_text = "next to each other and simultanious throws go in the same direction"
  } else if (trick.cascades === "separated_mirrored"){
    cascades_text = "next to each other and simultanious throws go in different directions"
  } else {
    cascades_text = "crossed such that the left most hand juggles a cascades with the center right hand and vice versa"
  }

  var sync = (trick.hands === "aabb" && trick.cascades === "crossed")
          || (trick.hands === "abab" && trick.cascades === "separated_parallel")
          || (trick.hands === "abba" && trick.cascades === "separated_mirrored")

  return `In this pattern, the jugglers stand ${hands_text} (hands: "${trick.hands}"). The two cascades are ${cascades_text} (cascades: "${trick.cascades}"). This results in a ${sync ? "sync" : "async"} pattern for each juggler.`
}

export const samePattern = (t1, t2) => (t1.hands === t2.hands && t1.cascades === t2.cascades); 
export const findPattern = (trick) => (patterns.filter(p => samePattern(trick, p))[0]); 

